.button{
  height: 60px;
  line-height: 49px;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  outline: none;
  letter-spacing: 1px;
}
.next-button{
  position: absolute;
  width: 100%;
  bottom: 0;
  right: 0;
  background-color: #52DA76;
  border-bottom: 8px solid #39aa57;
  z-index: 100;
}

.run-button:hover{
  background-color: #67f48c;
  cursor: pointer;
}

.run-button:focus,
.run-button:active{
  background-color: #52DA76;
  border-bottom: 3px solid #39aa57;
  height: 55px;
}

.run-button{
  position: absolute;
  width: 100%;
  bottom: 0;
  right: 0;
  background-color: #DD527F;
  border-bottom: 8px solid #8e2d4d;
  z-index: 100;
}

.run-button:hover,
.title-button:hover{
  background-color: #f5588a;
  cursor: pointer;
}

.run-button:focus,
.run-button:active,
.title-button:focus,
.title-button:active{
  background-color: #ce4471;
  border-bottom: 3px solid #8e2d4d;
  height: 55px;
}

.title-button.disabled
.run-button.disabled{
  color: #ce4471;
  background-color: #8e2d4d;
  border-bottom-color: #5f162e;
}
.lesson-container{
  /* height: 255px; */
  overflow: scroll;
}
.lesson{
  padding: 10px;
  text-align: center;
}

.lesson p, h1, h2 {
  text-align: left;
}

.lesson p{
  font-size: 12px;
  margin-top: 10px;
}

h1{
  font-size: 14px;
  margin-bottom: 2px;
  color: #DD527F;
}

h2{
  font-size: 20px;
  margin-bottom: 10px;
}

.task{
  color: #2c6b75;
  border: 1px solid #46a3a7;
  background-color: #76D6CE;
  padding: 18px 14px;
  font-weight: bold;
  border-radius: 2px;
  position: relative;
}

.task:before{
  content: "TASK";
  position: absolute;
  font-size: 8px;
  top: 0;
  left: 0;
  padding: 1px 5px 1px 3px;
  background-color: #46a3a7;
  color: #76D6CE;
  border-bottom-right-radius: 2px;
  border-top-left-radius: 1px;
}

.result{
  color: #DB5480;
  background-color: #ffbad0;
  padding: 10px 14px;
  font-weight: bold;
  border-radius: 2px;
}

.result-incorrect{
  color: #bf3804;
  background-color: #fbb8a4;
}

.result-success{
  color: #287b3e;
  background-color: #c9ffd7;
}

.title-screen h1{
  text-align: center;
}

.title-graphic{
  width: 400px;
  height: auto;
  margin-top: 50px;
}

.title-screen{
  background-color: #24252F;
  /* height: 100%; not working?*/
  height: 100vh;
  text-align: center;
}
.title-button{
  background-color: #DD527F;
  /* height: 50px; */
  display: block;
  width: 300px;
  margin: 0 auto;
  border-bottom: 8px solid #8e2d4d;
}

.title-screen p{
  color: #949494;
}



.title-button.disabled{
  margin-top: 20px;
  color: #ce4471 !important;
  background-color: #8e2d4d !important;
  border-bottom-color: #5f162e !important;
}

.title-button span{
  display: block;
  font-size: 8px;
}
.lesson pre{
  font-size: 11px;
}